@-webkit-keyframes animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }

  1% {
    background-position: 0 0;
    opacity: 1;

  }

  50% {
    background-position: 150px 0;
    opacity: 1;

  }



  100% {
    background-position: 400% 0;
    opacity: 1;

  }
    

  }

@keyframes animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }

  1% {
    background-position: 0 0;
    opacity: 1;

  }

  50% {
    background-position: 150px 0;
    opacity: 1;

  }



  100% {
    background-position: 400% 0;
    opacity: 1;

  }
    

  }


@-webkit-keyframes shring-animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }

  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }

  50% {
    background-position: 150px 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }

  100% {
    background-position: 400% 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }
}


@keyframes shring-animation {
  0% {
    background-position: 0 0;
    opacity: 0;
    width: 0;
  }

  1% {
    background-position: 0 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }

  50% {
    background-position: 150px 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }

  100% {
    background-position: 400% 0;
    opacity: 1;
    border-right: 1px solid #ff1d1d;
  }
}

@-webkit-keyframes type {
  0% {
    width: 0;
  }

  1%,
  99% {
    border-right: 1px solid #FFFFFF;
  }

  100% {
    border-right: none;
  }
}

@keyframes type {
  0% {
    width: 0;
  }

  1%,
  99% {
    border-right: 1px solid #FFFFFF;
  }

  100% {
    border-right: none;
  }
}

@-webkit-keyframes blink {
  50% {
    border-color: transparent;
  }
}

@keyframes blink {
  50% {
    border-color: transparent;
  }
}




/* ---- particles.js container ---- */

#tsparticles {
  width: 100%;
  height: 100%;
  background-color: #232741;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}
.button{

  height:2vh;
  cursor:pointer;
  z-index: 1;
  position:absolute;
}
.sun{
  position: -webkit-sticky;
  top: 0;
  margin-left:2vh;
  margin-top:2vh;
  position:fixed;
}
.containerprojects {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-left: -15.5%;
  margin-bottom: 2vh;
  margin-top: 2vh;
}

.box {
  border-radius: 8px;
  width: 40%;
  min-height: 40vh;
  margin: 10px;
  background-color: transparent;
  border: 1vh solid #ffffff;
  font-weight: 500;
  flex-grow: 1;
  align-self:stretch;
}

h3 {
  padding: 2vh;
  white-space: wrap;
}

h5 {
  margin-left: 2vh;
  font-size: 1px;
  white-space: wrap;
}

.projectText {
  padding: 2vh;
  margin-top: 0vh;
  font-size: 1.5vh;
  text-overflow: ellipsis;
  white-space: wrap;
}

.box p {
  max-width: 100%;
  text-overflow: ellipsis;
  white-space: wrap;
}

@media only screen and (max-width: 670px) {
  img:first-of-type{
    margin-top:12%;
  }
  #badge {
    display: block;
    padding-left: 0%;
    margin-top:2%;
    margin-bottom:2%;

  }
  .firstimg{
    margin-top:1%;
    margin-bottom:0%;
  }
}

@media screen and (max-width: 768px) {
  .containerprojects {
    flex-direction: column;
  }
  
  .box {
    width: 100%; /* updated to 90% */
    margin: 5%; /* updated to 5% */
  }

  #badge-proj {
    display: block;
    margin-top:2%;
    margin-bottom:4%;
  }
}
